@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1150px;
  padding-right: 30px;
  padding-left: 30px;
}
@media screen and (min-width: 768px) {
  .container {
    padding-right: 50px;
    padding-left: 50px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: "Opensans", "Arial", sans-serif;
  background-color: #63d1bb;
  padding: 10px 10px 10px 10px;
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  border: none;
}
@media screen and (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
  }
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.page {
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  background-color: #ffffff;
}

.page-body {
  color: #000000;
  background-color: #ffffff;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0 auto;
}

.page-body__header-wrapper {
  position: relative;
  width: 100%;
}
@media screen and (min-width: 1150px) {
  .page-body__header-wrapper::before {
    position: absolute;
    content: "";
    width: 50vw;
    height: auto;
    min-height: 694px;
    top: 0;
    right: 0;
    background-color: rgba(104, 183, 56, 0.85);
    z-index: 1;
  }
  .page-body__header-wrapper::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: auto;
    min-height: 694px;
    width: 50vw;
    z-index: 0;
    background-image: url("../img/decoration-images/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.header-main {
  max-width: 1150px;
  margin: 0 auto;
}
.header-main--no-js.container {
  padding-right: 0;
  padding-left: 0;
}
.header-main--no-js .header-main__logo-wrapper {
  padding-right: 20px;
  padding-left: 20px;
}

.header-main__logo-wrapper {
  display: flex;
  justify-content: center;
  padding-top: 22px;
  padding-bottom: 22px;
}
@media screen and (min-width: 768px) {
  .header-main__logo-wrapper {
    position: absolute;
    left: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo-wrapper {
    left: 50%;
    transform: translateX(-50%);
    padding-top: 9px;
    padding-bottom: 7px;
  }
}

.header-main__decor-wrapper {
  position: absolute;
  width: 100%;
  min-height: 88px;
  overflow: hidden;
}
.header-main__decor-wrapper::after {
  position: absolute;
  content: "";
  width: 500vw;
  height: 1px;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 5;
}

.header-main__logo {
  position: relative;
  text-decoration: none;
  width: 86px;
  height: 35px;
  background-image: url("../img/svg/logo-mobile.svg");
  background-repeat: no-repeat;
  z-index: 5;
}
@media screen and (min-width: 768px) {
  .header-main__logo {
    width: 113px;
    height: 46px;
    top: 0;
    background-image: url("../img/svg/logo-tablet.svg");
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo {
    width: 149px;
    height: 72px;
    background-image: url("../img/svg/logo-desktop.svg");
  }
}
.header-main__logo:hover {
  opacity: 0.8;
}
.header-main__logo:active {
  opacity: 0.6;
}

.header-main__logo-image--not-mobile {
  padding-left: 6px;
}
@media screen and (min-width: 768px) {
  .header-main__logo-image--not-mobile {
    color: #444444;
    padding-left: 5px;
    width: 67px;
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo-image {
    width: 38px;
    height: 22px;
  }
  .header-main__logo-image--not-mobile {
    width: 78px;
    padding-left: 8px;
  }
}

.main-nav {
  display: flex;
  background-color: transparent;
}
@media screen and (min-width: 768px) {
  .main-nav {
    width: 100%;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .main-nav__list--site-nav {
    grid-column: 2/3;
    grid-row: 1/2;
    grid-template-columns: 84px 1fr;
    padding-right: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list {
    background-color: transparent;
    max-width: 100%;
  }
  .main-nav__list--site-nav {
    grid-column: 3/4;
    grid-row: 1/2;
  }
}

@media screen and (min-width: 768px) {
  .main-nav__list--main-nav {
    padding-left: 50px;
    box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.1);
  }
  .main-nav__list--main-nav .main-nav__link {
    padding: 19px 0 19px 0;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list--main-nav {
    box-shadow: none;
    padding-left: 15px;
  }
  .main-nav__list--main-nav .main-nav__item {
    width: 220px;
    padding: 0 20px 0 35px;
  }
  .main-nav__list--main-nav .main-nav__item:nth-child(odd) {
    box-shadow: 1px 0 0 0 rgba(0, 0, 0, 0.1);
  }
}

.main-nav__list--customer-nav {
  display: none;
}
@media screen and (min-width: 1150px) {
  .main-nav__list--customer-nav {
    padding-left: 15px;
    display: grid;
    box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.1);
  }
  .main-nav__list--customer-nav .main-nav__item {
    width: 220px;
    padding: 0 20px 0 35px;
  }
  .main-nav__list--customer-nav .main-nav__item .main-nav__link {
    padding-top: 19px;
    padding-bottom: 19px;
  }
}

.main-nav__delivery {
  color: #444444;
  display: none;
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
}
@media screen and (min-width: 768px) {
  .main-nav__delivery {
    display: flex;
    justify-content: flex-end;
    padding-right: 50px;
    padding-top: 19px;
    box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.1);
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__delivery {
    grid-column: 2/4;
  }
}

.main-nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media screen and (min-width: 768px) {
  .main-nav__item {
    box-shadow: none;
  }
  .main-nav__list--site-nav .main-nav__item {
    min-height: 88px;
  }
}

@media screen and (min-width: 768px) {
  .main-nav__item--search {
    width: 84px;
    box-shadow: 1px 0 0 rgba(0, 0, 0, 0.1), inset 1px 0 0 rgba(0, 0, 0, 0.1);
  }
  .main-nav__item--search .main-nav__link-text {
    display: none;
  }
  .main-nav__item--search .main-nav__link {
    width: 84px;
    display: flex;
    justify-content: center;
  }
}

.main-nav__link {
  display: flex;
  width: 100%;
  align-items: center;
  padding: 28px 0 27px 76px;
  color: #000000;
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  text-decoration: none;
}
@media screen and (min-width: 768px) {
  .main-nav__link {
    padding-left: 0;
    font-size: 15px;
  }
}
.main-nav__list--site-nav .main-nav__link {
  padding-left: 0;
}
@media screen and (min-width: 1150px) {
  .main-nav__list--main-nav .main-nav__link {
    font-size: 17px;
  }
}
.main-nav__list--main-nav .main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__list--main-nav .main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link .main-nav__icon-img {
  opacity: 0.2;
}

.main-nav__list--site-nav .main-nav__link:hover .main-nav__icon-img {
  color: #63d1bb;
  opacity: 1;
}
.main-nav__list--site-nav .main-nav__link:hover .main-nav__link-text {
  opacity: 0.6;
}
.main-nav__list--site-nav .main-nav__link:active {
  opacity: 0.6;
}

@media screen and (min-width: 768px) {
  .main-nav__link-text {
    font-size: 17px;
  }
}

.main-nav__icon-img {
  width: 76px;
}
@media screen and (min-width: 768px) {
  .main-nav__icon-img {
    width: 82px;
  }
}

.main-nav__wrapper {
  position: relative;
}

.main-nav__toggle {
  position: absolute;
  top: 29px;
  right: 30px;
  width: 20px;
  height: 20px;
  background-image: url("../img/svg/close-menu.svg");
  background-color: inherit;
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  display: flex;
  border: none;
  cursor: pointer;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media screen and (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle.main-nav__toggle--close {
  right: 31px;
  top: 32px;
  width: 20px;
  height: 14px;
  background-image: url("../img/svg/open-menu.svg");
}

.main-nav__toggle--no-js {
  display: none;
}

.pop-up-menu {
  display: none;
}
@media screen and (min-width: 768px) {
  .pop-up-menu {
    display: grid;
    grid-template-columns: 48% 52%;
    grid-auto-flow: dense;
    width: 100%;
  }
}
@media screen and (min-width: 1150px) {
  .pop-up-menu {
    grid-template-columns: 450px 1fr 400px;
  }
}
.pop-up-menu::before {
  content: "";
  width: 100%;
}
@media screen and (min-width: 768px) {
  .pop-up-menu::before {
    grid-column: 1/2;
    grid-row: span 1;
    padding-left: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .pop-up-menu::before {
    grid-column: 2/3;
    grid-row: 1/2;
    padding-left: 0;
  }
}

.pop-up-menu--is-open {
  position: absolute;
  z-index: 10;
  display: flex;
  flex-direction: column;
  width: 100%;
  left: 0;
}
@media screen and (min-width: 768px) {
  .pop-up-menu--is-open {
    z-index: 0;
    position: static;
    display: grid;
  }
}

.pop-up-menu--no-js {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .pop-up-menu--no-js {
    padding-top: 0;
  }
}

.pop-up-menu__button-close {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 22px;
  height: 22px;
  padding: 0;
  display: flex;
  border: none;
  background-color: transparent;
  background-image: url("../img/svg/icon-cross.svg");
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .pop-up-menu__button-close {
    display: none;
  }
}
.pop-up-menu__button-close:hover {
  opacity: 0.6;
}
.pop-up-menu__button-close:active {
  opacity: 0.3;
}

.main-footer {
  background-color: #f1f1f1;
  margin-top: auto;
  padding-top: 0;
  padding-bottom: 23px;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .main-footer {
    padding-top: 51px;
    padding-bottom: 58px;
  }
}
@media screen and (min-width: 1150px) {
  .main-footer {
    padding-top: 66px;
    padding-bottom: 66px;
  }
}

.main-footer__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .main-footer__wrapper {
    flex-direction: row;
    justify-content: space-between;
  }
}

.main-footer__logo {
  display: none;
  justify-content: center;
  text-decoration: none;
  color: #000000;
  width: 100%;
  border-bottom: 1px solid #d9d9d9;
  padding-bottom: 20px;
}
.main-footer__logo:hover {
  color: #46c1ae;
}
.main-footer__logo:active {
  opacity: 0.3;
}
@media screen and (min-width: 768px) {
  .main-footer__logo {
    display: flex;
    width: initial;
    border-bottom: none;
    padding: 0;
    margin-top: 5px;
  }
}

.main-footer__socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  padding: 6px 0;
  border-bottom: 1px solid #d9d9d9;
  margin-bottom: 20px;
}
@media screen and (min-width: 768px) {
  .main-footer__socials {
    width: initial;
    border-bottom: none;
    padding: 0;
    margin: -5px 0 0 24px;
  }
}

.main-footer__copyrights {
  text-decoration: none;
  color: #000000;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 182px;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .main-footer__copyrights {
    margin: 0;
  }
}
.main-footer__copyrights .main-footer__copyrights-logo-img {
  fill: #63d1bb;
}
.main-footer__copyrights:hover .main-footer__copyrights-logo-img, .main-footer__copyrights:focus .main-footer__copyrights-logo-img {
  fill: #46c1ae;
}
.main-footer__copyrights:active .main-footer__copyrights-logo-img {
  opacity: 0.3;
}

.main-footer__copyrights-text {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  font-family: "Opensans", "Arial", sans-serif;
  padding-top: 8px;
}

.socials {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, 28px);
  column-gap: 24px;
  row-gap: 6px;
  max-width: 210px;
}

.socials__link {
  display: flex;
  width: 28px;
  height: 48px;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  color: #231f20;
}
.socials__link:hover {
  color: #46c1ae;
}
.socials__link:active {
  opacity: 0.3;
}

.map {
  position: relative;
  display: grid;
}
@media screen and (min-width: 1150px) {
  .map {
    grid-template-columns: 1fr minmax(1024px, 1440px) 1fr;
  }
}

.map--not-index .map__contacts {
  background-color: #f1f1f1;
}
@media screen and (min-width: 1150px) {
  .map--not-index .map__contacts {
    background-color: #ffffff;
  }
}
@media screen and (min-width: 1150px) {
  .map--not-index .contacts {
    padding: 59px 109px 44px 80px;
  }
}
@media screen and (min-width: 1150px) {
  .map--not-index .contacts__address {
    max-width: 126px;
  }
}
@media screen and (min-width: 1150px) {
  .map--not-index .contacts__street {
    margin-bottom: 0;
  }
}

.map__image-wrapper {
  grid-column: 1/-1;
  grid-row: 2/4;
  height: 100%;
  width: 100%;
}

.map__image {
  display: flex;
  width: 100%;
  object-fit: cover;
}
@media screen and (min-width: 768px) {
  .map__image {
    min-height: 400px;
  }
}

.map__frame {
  grid-column: 1/-1;
  grid-row: 2/4;
  display: flex;
  width: 100%;
  border: none;
  position: relative;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .map__frame {
    height: 400px;
  }
}

@media screen and (min-width: 1150px) {
  .map__contacts-wrapper {
    grid-column: 2/3;
    grid-row: 2/4;
    z-index: 5;
    width: 100%;
    margin: 0 auto;
    min-height: 198px;
    height: auto;
    pointer-events: none;
  }
}

@media screen and (min-width: 1150px) {
  .map__contacts {
    max-width: 565px;
    background-color: #ffffff;
    margin: 95px 0 107px 7.64%;
    padding: 56px 60px 56px 80px;
    pointer-events: all;
  }
}

.contacts {
  display: flex;
  justify-content: space-between;
  padding: 26px 20px;
}
@media screen and (min-width: 768px) {
  .contacts {
    padding: 60px 70px;
  }
}
@media screen and (min-width: 1150px) {
  .contacts {
    padding: 59px 75px 61px 80px;
  }
}

.contacts__title {
  font-family: "Opensans", "Arial", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  min-width: 140px;
  max-width: 140px;
  color: #111111;
  margin: 0;
}
@media screen and (min-width: 768px) {
  .contacts__title {
    font-size: 20px;
    line-height: 26px;
    max-width: 171px;
  }
}

.contacts__address {
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  max-width: 160px;
  color: #444444;
  font-style: normal;
}
@media screen and (min-width: 768px) {
  .contacts__address {
    text-align: right;
    font-size: 16px;
    margin-top: -5px;
  }
}
@media screen and (min-width: 1150px) {
  .contacts__address {
    text-align: left;
  }
}

@media screen and (min-width: 768px) {
  .contacts__street {
    display: block;
    margin-bottom: 19px;
  }
}